<style lang="scss">
// ==============================
// Intro Animation
// ========================================

.intro-animation {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: $color-white;
  z-index: $intro-animation-zindex;
}

// ==============================
// Intro Animation Logo
// ========================================

.intro-animation__logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
}

// ==============================
// Logo animation path
// ========================================

.animation-path {
	fill: none;
	stroke-dasharray: 45000;
	stroke-dashoffset: 45000;
}
</style>

<script>
// TimelineLite
import { TimelineLite, Sine } from 'gsap'

export default {
  name: 'IntroAnimation',
  mounted () {
    // Run Animation
    //
    this.runAnimation()

    // Set cookie to hide on next visit
    //
    if (this.$cookie.get('introAnimationShown') !== 'true') {
      this.$cookie.set('introAnimationShown', 'true', 5)
    }
  },
  methods: {
    runAnimation: function () {
      const self = this

      // Prevent flash of SVG before animation, by setting inline
      // CSS to hide logo on load, then showing it on mounted()
      //
      // eslint-disable-next-line
      setTimeout (() => {
        this.$refs.introAnimationInner.style.opacity = 1
      }, 500)

      const introAnimation = new TimelineLite()
      const introContainerAnimation = new TimelineLite({ onComplete: removeIntro })

      // Animate path to draw logo
      //
      introAnimation.to('.animation-path', 8.5, {
        // eslint-disable-next-line
        ease: Sine.easeOut,
        strokeDashoffset: 34500,
        delay: 1.5
      })
      introAnimation.play()

      // Then animate the whole component out
      //
      introContainerAnimation.to('.intro-animation', 1, {
        opacity: 0,
        delay: 6.5
      })
      introContainerAnimation.play()

      // Remove node from the dom
      //
      function removeIntro () {
        self.$refs.introAnimation.outerHTML = ''
      }
    }
  }
}
</script>
